//api
import { emailValidate } from "@/api/login";

export default {
  data() {
    return {
      uuid: "",
      loading: true,
      success: "",
    };
  },

  //创建的时候自动调用
  created() {
    this.uuid = this.$route.params.uuid;
    this.email(this.uuid);
  },
  //创建后
  mounted() {},
  methods: {
    //获取所有内容
    email(uuid) {
      emailValidate(uuid)
        .then((response) => {
          this.loading = false;
          this.success = response.data;
        })
        .catch((response) => {});
      this.goHome();
    },
    goHome() {
      let count = 3; //赋值多少秒
      var times = setInterval(() => {
        count--; //递减
        if (count <= 0) {
          // <=0
          this.$router.push({ path: "/" });
          clearInterval(times);
        }
      }, 1000); //1000毫秒后执行
    },
  },
};
